import Vue from 'vue';
import Vuex from 'vuex';
import getters from "@/store/getters";
import mutations from "@/store/mutations";
import actions from "@/store/actions";

Vue.use(Vuex);

const state ={
  playlistInfo: {}, // 
  playerList: [],   // 
  currentPlayID: null, // ID
  currentPlayIndex: 0, //  index
  currentPlayUrl: '', //   url
  currentPlayLrc: '', //   lrc
  isPlay: false,  // player
  isShow: false,  // player
  playMode: 0, // player
  progressTime: 0, // 
  progressRate: 0, // 
  progressMove: 0, // 
  duration: 0, // 
  audio: null, // 
  collectSongs: [], // 
  colSongList: [], // 
};

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});

export default store;